body{
  background-size: cover;
  align-items: center;
  text-align: center;
background: linear-gradient(-45deg, #ee7752, #e73c7e, #23a6d5, #23d5ab);
    background-size: 400% 400%;
    animation: gradient 15s ease infinite;
    overflow: hidden;
}

@keyframes gradient {
    0% {
        background-position: 0% 60%;
    }
    50% {
        background-position: 100% 50%;
    }
    100% {
        background-position: 0% 50%;
    }
}
.ui.form {
 
  width: 204px;
  height: 220px;
  margin:0px;
  border:none;
  border-radius: 15px;
  padding: 50px 100px 80px 40px;
  background-color: rgba(0,0,0,0.5);
  position: relative;
  display: inline-block;
  box-shadow: 3px 3px 10px rgb(0, 0, 0),
  -3px -3px 16px #ddd;
}
.container{
  padding-top: 25%;
}
label{
  color: rgb(248, 248, 248);
  
}
.fa-user-o{
  color: #eee;
}
.fa-envelope-o{
  color: #eee;
}
p {
  color:red;
  padding-top:2px;
  text-align: left;
  font-size: 12px;
  
}


div.field{
  font-size: 15px;
  text-align: left;
}

.fa-user-o{
  padding: 5px;
}
.fa-envelope-o{
  padding: 5px;
}

.input{
  border-radius:10px;
  width: 240px;
  height: 40px;
  margin-left: 0px;
  padding-left: 15px;
  display: inline-block;
  border:none;
  outline: none;
 
}
.input:hover{
  box-shadow: 3px 3px 5px rgb(224, 224, 224);
}
.input:invalid{
  color: red;
  animation: shake 200ms;
}@keyframes shake{
  0%{
    transform: translateX(4px);
  }
  25%{
    transform: translateX(-4px);
  }
  50%{
    transform: translateX(4px);
  }
  75%{
    transform: translateX(-4px);
  }
  95%{
    transform: translateX(4px);
  }
}
.custom-btn {
  width: 60%;
  height: 40px;
  margin-left: 50px;
  color: #fff;
  border-radius: 5px;
  font-family: 'Lato', sans-serif;
  background: transparent;
  cursor: pointer;
  transition: all 0.3 ease;
  position: relative;
  display: inline-block;
  box-shadow: 2px 2px 16px rgb(0, 0, 0),
  -3px -3px 6px #ddd;
  outline: none;
  align-items: center;
 }
 
 .btn-1 {
  background: rgb(55, 57, 86);
  background: linear-gradient(0deg,rgb(232, 28, 28) 0%, rgb(157, 49, 49) 100%);
   
  border: none;
 }
 
 .btn-1:hover {
  background: rgb(255, 0, 0);
  background: linear-gradient(0deg, rgb(40, 233, 32) 0%, rgb(92, 174, 113) 100%);
 }


div.ui.message.sucess{
  color: rgb(18, 241, 10);
  text-align: center;
  margin-top: 10px;
}
h1{
  color: rgb(255, 255, 255);
  border-bottom: rgb(245, 0, 0);
  font-weight: 500px;
}
h2{
  color: greenyellow;
}
